* {
  margin: 0;
}

.header_Image {
  width: 90px;
  object-fit: contain;
}

.App {
  background-color: #fafafa;
}

.app_header {
  position: sticky;
  top: 0;
  z-index: 1;

  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgrey;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
}

.app_posts {
  padding: 20px;
}

.app_signUp {
  display: flex;
  flex-direction: column;
}
